import { createRouter, createWebHistory } from "vue-router";

import Cart from '@/views/cart/Cart';
import Product from '@/views/product/Product';

const routes = [

    {
        path: "/shop/cart",
        name: "Cart",
        component: Cart
    },
    {
        path: "/shop/product",
        name: "Product",
        component: Product
    },

];

const router = createRouter({
    history: createWebHistory(),
    routes
})

export default router;